<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label >博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories">
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">
          {{author}}
        </option>
      </select>
      <!-- v-on:click.prevent 阻止刷新页面及默认提交 -->
      <button v-on:click.prevent="post">编辑博客</button>
    </form>
    
    <div v-if="submmited">
      <h3>您的博客编辑成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类 ：</p>
      <ul>
        <li v-for="category in blog.categories" :key="category" >         
          {{category}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>

  </div>
</template>

<script>
// http://jsonplaceholder.typicode.com/
// http://jsonplaceholder.typicode.com/posts
export default {
  name: 'add-blog',
  data () {
    return {
      id:this.$route.params.id,
      blog:{},
      authors:["Neptune","KEN","Well"],
      submmited:false
      
    }
  },
  methods:{
    fetchData(){
        // console.log(this.id);
        // this.$http.get("http://jsonplaceholder.typicode.com/posts/"+this.id)
        //           .then(response=>{
        //             //   console.log(response.body);
        //             this.blog = response.body;
                      
        //           })
         const query = Bmob.Query('myblog');
            query.get(this.id).then(res => {
                // console.log(res)
                 this.blog = res;
            }).catch(err => {
                console.log(err)
            })
        

    },
    post:function(){
    //   this.$http.put("http://jsonplaceholder.typicode.com/posts/"+this.id,this.blog)
    //     .then(function(data){
    //       console.log(data);
    //       this.submmited = true;
          
    //     });

     const query = Bmob.Query('myblog');
        query.set('id', this.id) //需要修改的objectId
        query.set("title",this.blog.title)
        query.set("content",this.blog.content)
        query.set("author",this.blog.author)
        query.set("categories",this.blog.categories)
        query.save().then(res => {
        console.log(res)
        this.submmited = true;
        }).catch(err => {
        console.log(err)
 })
    }
  },
  created(){
      this.fetchData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;  
}

#add-blog{
  margin:20px auto;
  max-width: 600px;
  padding: 20px;
}
label{
  display: block;
  margin:20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px
  
}

textarea{
  height: 200px;
}

#checkboxes label{
  display: inline-block;
  margin-top: 0
}

#checkboxes input{
  display: inline-block;
  margin-right: 10px
}
button{
  display: block;
  margin:20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  /* cursor: pointer 鼠标放上去是手指的样子 */
  cursor: pointer;

}
#preview{
  padding: 10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
}
h3{
  margin-top: 10px
}
</style>



