<template>
  <div id="show-blogs" v-theme:column="'narrow'">
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索" v-model="search">
    <!-- 这里不能像以前没过滤那样 遍历 blogs，要遍历computed里的 -->
    <div v-for="blog in filteredBlogs" class="single-blog" :key="blog.id">
        <router-link :to="'/blog/' + blog.id">
          <h2 v-rainbow>{{blog.title }}</h2>
        </router-link>
        <article>
            {{blog.content | snippet }}
        </article>
        <p>{{blog.categories}}</p>
        <p>{{blog.author}}</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
      return {
          blogs:[],
          search:""
      }
  },
  created(){
    //    this.$http.get("https://jsonplaceholder.typicode.com/posts")

      // this.$http.get('../../static/posts.json')
      //     .then(function(data){
      //       // console.log(data);
      //       // 只获取10条数据
      //       this.blogs = data.body.slice(0,10);
      //       // console.log(this.blogs);
            
      //     })
        const query = Bmob.Query("myblog");
          query.find().then(res => {
          // this.blogs = res.body.slice(0,10);
          for (let i=0;i< res.length;i++){
              // console.log(res[i]);
              // console.log(res[i].author);
              // console.log(res[i].title);
             
              res[i].id =res[i].objectId;                   
              this.blogs.push(res[i]);
              // this.blogs.push(res[i]);
          }
          console.log(this.blogs);
});

  },
  computed:{
      filteredBlogs:function(){
        //   blog1 也不报错
        return this.blogs.filter((blog) =>{
            return blog.title.match(this.search); 
        })  
      }
  },
  filters:{
//方法一
    //   "to-uppercase":function(value){
    //      return value.toUpperCase();
    //   }
// 方法二
      toUpperCase(value){
         return value.toUpperCase();
      }
  },
  directives:{
      'rainbow':{
          bind(el,binding,vnode){
              el.style.color="#" + Math.random().toString(16).slice(2,8);
          }
      }
  }

  
}
</script>

<style scoped>
#show-blogs{
    max-width: 800px;
    margin:0 auto;
}
.single-blog{
    padding: 20px;
    margin:20px 0;
    box-sizing: border-box;
    background: #eee;
    border: 1px dotted #aaa;
}

#show-blogs a{
  color: #444;
  text-decoration: none;
}
input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



