<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>
            {{blog.content}}
        </article>
        <button @click="deleteSingleBlog()">删除</button>
        <router-link :to="'/edit/' + id">编辑</router-link>
    </div>
</template>

<script>
export default {
    name:"single-blog",
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        
        // this.$http.get("../../static/posts.json/"+this.id)
        //  this.$http.get("http://jsonplaceholder.typicode.com/posts/"+this.id)
        //     .then(function(data){
        //         // console.log(data);   
        //         this.blog = data.body;            

        //     })

        const query = Bmob.Query('myblog');
            query.get(this.id).then(res => {
                // console.log(res)
                 this.blog = res;
            }).catch(err => {
                console.log(err)
            })
    },
    methods:{
        deleteSingleBlog(){
            // this.$http.delete("http://jsonplaceholder.typicode.com/posts/"+this.id)
            //           .then(response =>{
            //               this.$router.push({path:'/'})
            //           })
            const query = Bmob.Query('myblog');
                query.destroy(this.id).then(res => {
                console.log(res)
                this.$router.push({path:'/'})
                    }).catch(err => {
                console.log(err)
})
        }
    }
}
</script>

<style scoped>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa
}
</style>





